.page {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

.body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  text-transform: uppercase;
  font-weight: 500;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__link {
  color: #060b35;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.is-active:after:hover {
  color: #00acdc;
}

.section {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 49px 46px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .section {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .section {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .section {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  transition: transform .3s;
}

.card__logo {
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.card__info {
  color: #060b35;
  margin: 36px 16px 16px;
}

.card__info-title {
  width: 166px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info-text {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__info-rate {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 24px;
  display: flex;
}

.card__info-price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.card__info-button {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  width: 100%;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__info-button:hover {
  color: #00acdc;
  background-color: #fff;
  outline: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__info-title {
  color: #00acdc;
}

.card:hover .card__info-button {
  color: #00acdc;
  background-color: #fff;
  outline: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

.rate-reviews {
  color: #616070;
  flex-direction: column;
  justify-content: flex-end;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.price {
  margin: 0;
  line-height: 18px;
}

.card__info-price-text {
  color: #616070;
  font-size: 12px;
}

.card__info-price-money {
  font-size: 16px;
  font-weight: 700;
}

