.nav {
  font-weight: 500;
  text-transform: uppercase;

  &__list {
    display: flex;
    gap: 20px;
    padding: 0;
    margin: 0;
  }

  &__item {
    list-style: none;
  }

  &__link {
    display: block;
    text-decoration: none;
    font-size: 12px;
    color: $nav-text-color;
    line-height: 60px;
    position: relative;

    &:hover {
      color: $nav-hover-text-color;
    }

  }
}

.is-active {
  color: $nav-hover-text-color;
}

.is-active::after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 8px;
  background-color: $nav-hover-text-color;
  position: absolute;
  bottom: 0;

  &:hover {
    color: $nav-hover-text-color;
  }
}
